* {
    box-sizing: border-box;
    margin: 0;
  }
  div{
    border-radius: 15px;
  }
  .top { 
    background-color: aquamarine;
    width: 50%;
    height: 500px;
    z-index: 1;
    margin-left: 25%;
    margin-top: 12.5%;
  }
  
  .header {
    padding-top: 35px;
  }
  .header h1{
    margin-left: 26%;
  }
  
  .header p {
    margin-top: 10px;
    margin-left: 20%;
    font-size: 25px;
  }
  
  form {
    margin-top: 100px;
    margin-left: 20%
  }
  
  input {
    border: none;
    border-radius: 20px;
    width: 556px;
    height: 78px;
    cursor: pointer;
  }
  
  ::placeholder{
    font-size: 14px;
  }
  
  input[type=email], input[type=password] {
    font-size: 14px;
    padding-left: 10px;
  }
  
  button {
    border: none;
    width: 100px;
    height: 50px;
    margin-top: 15px;
    margin-left: 15%;
    cursor: pointer;
    background-color: #ffffff;
    border-radius: 15px;
  }
  
  button:hover{
    background-color: #000000;
    transition: 0.5s;
    
  }
  
  a {
    text-decoration: none;
    color: #000000;
  }
  
  a:hover{
    color:#ffffff
  }